.HomePage{
    display: flex;
    padding-top: 20px;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}
.MovingButtons{
    border-bottom-style:solid;
    border-bottom-width: 0.1px;
    border-bottom-color:wheat;
    align-content: center;
    display: flex;
    justify-content: center;
}
.Button{
   
   border-radius: 100px;
   background-color:silver;
   border-style:hidden;
   padding: 3px;
   margin: 10px 6px;
}
.Button:hover{
    background-color:grey;
    cursor: pointer;
}
.Trending{
    padding-top: 20px;
    font-size: 25px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    padding-left: 20px;
}
.Products{
    display: flex;
    border-bottom-style:solid;
    border-bottom-width: 0.1px;
    border-bottom-color:wheat;
    padding-bottom: 20px;
    justify-content: space-around;
    
}
.Product{
    display: block;
    height: 45vh;
    width: 27vw;
    text-align: center;
    margin-left: 2px;
}
.Pointers{
    height: 35vh;
    padding-top: 14vh;
}
.icons
{
    height: 35px
}
.Pic{
    height: 35vh;
    width: 27vw;
}
.Desc{
    font-size: 1.5vw*2px;
    font-family: 'Times New Roman', Times, serif;
}