body
{
  background-color:white
}
.Navbar{
    border-bottom-style:solid;
    border-bottom-width: 0.1px;
    border-bottom-color:wheat;
    border-top-style:solid;
    border-top-width: 0.1px;
    border-top-color:wheat;
    display: flex;
    justify-content: space-between;
    background-color: white;
    padding-left: 20px;
    overflow: hidden;
}
.NavbarTabs{
    display: flex;
    color:black;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px; 
    padding-left: 10px;
    overflow: hidden;
  }
.LeftNav{
    display: flex;
    padding-left: 10px;
    align-items: left;
  }
.MidNav{
    display: flex;
    padding-left: 10px;
    align-items: center;
}
.RightNav{
    display: flex;
    align-items: center;
    padding-right: 20px;
  }
.Links{
    text-decoration: none;
    color: black;
  }
.Links:hover{
    text-decoration: none;
    cursor: pointer;
  }
#Logo{
  color: coral;
  font-size: 30px;
  font-weight:bolder;
  display: inline-block;
}
.ResponsiveBar{
  display: none;
}
@media screen and (max-width: 830px) {
  .RightNav{
    display: none;
  }
  .LeftNav{
    display: none;
  }
  .ResponsiveBar{
    display: flex;
    padding-left: 10px;
    align-items: center;
    padding-right: 10px;
  }

  /* Dropdown Content (Hidden by Default) */
  .dropdown-content {
  display: none;
  }
  
/*
  .dropdown-content div {
    color: black;
    text-decoration: none;
    display: block
    
  }
*/
  /* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
  .show 
  {
    position:absolute;
    display:block;
    right: 0px;
    top: 0px;
    bottom: 0px;
    background-color:white;
    width: 150px;
    border-color:wheat;
    border-style: solid;
    border-width: 0.1px;
  }

}

.RespOpts{
  display: block;
  border-bottom-style:solid;
  border-bottom-width: 0.1px;
  border-bottom-color:wheat;
  padding-top: 10px;
  padding-left: 10px;
  padding-bottom: 10px;
}
.Hamburger {
  width: 15px;
  height: 2px;
  background-color: black;
  margin: 4px 0;
}